<template>
    <div 
      class="semester_label_tag" 
      :style="getStyleForLabel(label)"
    >
      {{ label }}
    </div>
  </template>
  
  <script>
  export default {
    props: {
      label: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        labelOptions: []
      }
    },
    async created() {
        if(this.$store.state.semester_label.length<=0) {
            await this.$store.dispatch('fetchSemesterLabel')
        }
        this.labelOptions = this.$store.state.semester_label.data
    },
    methods: {
      getLabelColor(label) {
        const option = this.labelOptions.find(opt => opt.label === label);
        return option ? option.color : 'defaultColor'; 
      },
      getStyleForLabel(label) {
        const color = this.getLabelColor(label);
        return `background-color: ${color}44; color: ${color}; border: 1px solid ${color}`;
      }
    }
  }
  </script>
  
<style lang="sass">
@import "@/style/common.sass"
</style>
  