<template lang="pug">
.page-container
    .page-title 個案列表
    el-table.list-container(:data="cases" style="width: 100%" :height="tableHeight")
        el-table-column(prop="name" label="姓名")
            template(#header)
                el-input(v-model="search" size="small" prefix-icon="el-icon-search" placeholder="輸入姓名搜尋")
        el-table-column(prop="caseId" label="學號")
        el-table-column(prop="semester_label" label="學期標籤")
            template(#default='scope')
                SemesterLabelTag(:label="scope.row.semester_label")
        el-table-column(prop="available_time" label="可諮商時段")
            template(#header='')
                el-popover(placement="right" title="" :width="200" trigger="hover" popper-class="weektimePop")
                    div(v-html="getHtmlContent()")
                    template(#reference)
                        span 可諮商時段
                            QuestionFilled(style="width: 1em; height: 1em; padding: 0 0 0 3px")
            template(#default='scope')
                div {{scope.row.available_time}}
        el-table-column(prop="crisis_level" label="危機程度" sortable :sort-method="levelRule")
        el-table-column(prop="init_counselor_name" label="初談老師")
        el-table-column(prop="discription" label="狀態說明")
        el-table-column(prop="init_consultation_date" label="初談時間" sortable)
            template(#default='scope')
                div {{convertUTCtoStr(scope.row.init_consultation_date)}}
        el-table-column(label="功能按鈕")
            template(v-slot:default="slotProps") 
                el-button(type="primary" size="small"  @click="modifyCase(slotProps.row.id)") 修改
                el-button(type="danger" size="small" @click="clickDelete(slotProps.row)") 刪除
</template>

<script>
import {weektimeMixin} from "@/components/weektimeMixin"
import SemesterLabelTag from '@/components/SemesterLabelTag.vue';
export default {
    components: {SemesterLabelTag},
    mixins: [weektimeMixin],
    data () {
        return {
            search: ''
        }
    },
    async created() {
        await this.$store.dispatch('fetchUnassignCases')
    },
    computed: {
        cases() {
            return this.$store.state.unassignCases.filter(
                (data) =>
                !this.search ||
                data.name.toLowerCase().includes(this.search.toLowerCase())
            )
        },
        tableHeight() {
            return window.innerHeight - 150
        }
    },
    methods: {
        deleteCase(id) {
            this.$store.commit('SET_STATUS', 'pending')
            this.$http.delete("/api/cases/"+id)
            .then(response => {
                console.log(response.data.message)
                this.$message({
                    type: 'success',
                    message: '刪除成功!'
                });
                this.$store.dispatch('fetchUnassignCases')
                this.$store.commit('SET_STATUS', 'end')
            })
            .catch((error) => {
                //發送請求失敗時
                console.log(error)
            });
        },
        clickDelete(caseData) {
            this.$confirm('將永久刪除學號為 '+caseData.caseId+' 的資料, 是否繼續?', '提示', {
                confirmButtonText: '確定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    this.deleteCase(caseData.id)
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消刪除'
                })         
            })
        },
        modifyCase(id) {
            this.$router.push('/modifyCase/'+id)
        },
        levelRule(aObj,bObj) {
            var a = aObj.crisis_level
            var b = bObj.crisis_level
            var aCount = 0
            var bCount = 0
            var labelNumber = {
                A: 50,
                B: 30,
                C: 10
            }
            var levelNumber = {
                '+': 5,
                '-': -5
            }
            for (let l in labelNumber){
                if(a.indexOf(l)>=0) aCount+=labelNumber[l]
                if(b.indexOf(l)>=0) bCount+=labelNumber[l]
            }
            for (let l in levelNumber){
                if(a.indexOf(l)>=0) aCount+=levelNumber[l]
                if(b.indexOf(l)>=0) bCount+=levelNumber[l]
            }
            return aCount-bCount
        }
    }
}
</script>
<style>
.weektimePop {
    padding: 10px !important;
    background-color: #F0F2F5 !important;
    border-color: #F0F2F5 !important;
    color: #303133 !important;
}
.weektimePop .el-popper__arrow::before {
    border-top-color: #F0F2F5 !important;
    border-color: #F0F2F5 !important;
    background-color: #F0F2F5 !important;
}
.weektimePop > div > table {
    border: 0;
    border-collapse: collapse;
    width: 100%;
    margin: 10px 0;
}
.weektimePop > div > table > tbody > tr > td {
    border: 1px #909399 solid !important;
    padding: 5px 10px;
}
</style>
<style lang="sass" scoped>
@import "@/style/common.sass"    
</style>
